import React, { useEffect, useState } from 'react'
import 'bootstrap/dist/css//bootstrap.min.css'
import CodeIcon from '@mui/icons-material/Code';
import { NavLink, useHistory } from 'react-router-dom'
import axios from 'axios';

function ForgotPassword() {


    useEffect(() => {
        document.title = "Forgot Password || PrepAvenger";
    }, [])

    const [email, setemail] = useState({
        userEmail: ""
    });

    const [otp, setotp] = useState("");
    const [loading, setloading] = useState(true);

    const history = useHistory();



    const sendOtp = (e) => {
        setloading(false);
        console.log(email);

        axios.post(`/sendotp`, email).then(
            (success) => {

                console.log(success);
                setotp(success.data);
                document.getElementById("otpfield").style.display = "block";
                document.getElementById("sendotp").style.display = "none";
                document.getElementById("verifyotp").style.display = "block";
                document.getElementById("signupalert").style.display = "block";
                document.getElementById("signupalert").classList.add("alert-success");
                document.getElementById("errsuc").innerHTML = "Success!!!";
                document.getElementById("desc").innerHTML = "Otp sent successfully! Kindly check your mail!";
                setloading(true);
            },
            (error) => {

                document.getElementById("signupalert").style.display = "block";
                document.getElementById("signupalert").classList.add("alert-danger");
                document.getElementById("errsuc").innerHTML = "Error!!!";
                document.getElementById("desc").innerHTML = "Oops something went wrong! Please enter your correct password!";
                console.log(error);
                setloading(true);
            }
        );

        e.preventDefault();

    }
    const verifyOtp = (e) => {
        // console.log(document.getElementsById("otpfield"));
        if (document.getElementById("otpfield").value == otp) {
            localStorage.setItem("email", email.userEmail);
            history.push("/passwordreset");
            // window.location("/resetpassword");
        }
        else {
            // history.push("/forgotpassword");
            // window.location("/forgotpassword");
            document.getElementById("signupalert").style.display = "block";
            document.getElementById("signupalert").classList.add("alert-danger");
            document.getElementById("errsuc").innerHTML = "Error!!!";
            document.getElementById("desc").innerHTML = "Oops something went wrong! Otp doesn't match!";
        }
        e.preventDefault();
    }

    return (
        <>
            <div className="container">
                <NavLink to="/" style={{ 'textDecoration': 'none' }}>
                    <h1 className="text-primary text-bold text-center mb-4"> &lt; PrepAvenger /&gt; </h1>
                </NavLink>
                <h2 className="text-primary text-center">For Coders</h2>

                <p className="text-light text-center"> <CodeIcon />Practice coding, prepare for interviews, and get hired!!!</p>
                <div className="alert alert-dismissible fade show w-40" id="signupalert" role="alert">
                    <strong id="errsuc"></strong> <span id="desc">  </span>
                    <button type="button" className="btn-close" data-bs-dismiss="alert" aria-label="Close"></button>
                </div>
                {
                    loading ?
                        <p style={{ "display": "none" }}></p>
                        :
                        <p className="text-center my-4" id="spinner">
                            <div class="spinner-grow text-primary" role="status">
                                <span class="visually-hidden">Loading...</span>
                            </div>
                        </p>
                }
                <div className="formcontainer d-flex justify-content-center align-items-center h-100">
                    <div className="mt-5">
                        <form>
                            <h1 className="text-light text-center mb-4" >Forgot Password</h1>
                            <div className="mb-3">
                                <input type="email" className="form-control bg-dark" id="exampleInputEmail1" aria-describedby="emailHelp" placeholder="Email"
                                    onChange={(e) => {
                                        setemail({ ...email, userEmail: e.target.value })
                                    }}
                                    value={email.userEmail}
                                />
                            </div>
                            <div className="mb-3">
                                <input type="text" className="form-control bg-dark" aria-describedby="emailHelp" placeholder="OTP" id="otpfield" style={{ "display": "none" }}
                                />
                            </div>
                            <p className="text-center">
                                <button type="submit" className="btn btn-outline-primary text-center my-4 w-100" onClick={sendOtp} id="sendotp">Send Otp</button>
                            </p>
                            <p className="text-center">
                                <button type="submit" className="btn btn-outline-primary text-center my-4 w-100" style={{ "display": "none" }} id="verifyotp" onClick={verifyOtp}>Verify</button>
                            </p>
                        </form>
                    </div>
                </div>
            </div>
        </>
    )
}

export default ForgotPassword
